$header-size: 150px;
$header-size-mobile: 110px;
$burger-breakpoint: 989px;
$header-size-breakpoint: 782px;
$announcement-banner: 66px;

html{

	.hamburger-overlay{
		background: rgba($color: $black, $alpha: 0.7);
		position: fixed;
		z-index: 10;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		opacity: 0;
		transition: opacity ease-in-out 0.15s;
		pointer-events: none;
		//backdrop-filter: blur(1px);
	}

	.hamburger-content{
		position: fixed;
		width: 100%;
		pointer-events: none;
		right: 0;
		z-index: 11;
		top: $header-size-mobile;
		height: calc( 100vh - #{$header-size-mobile} );
		transition: all ease 0.25s;
		opacity: 0;
		visibility: hidden;
		background: $white;
		background: linear-gradient(#c84bf4 0%, #6a88df 100%);
		@media only screen and (min-width: #{$header-size-breakpoint+1}){
			height: calc( 100vh - #{$header-size} );
			top: $header-size;
			width: 45%;
			right: -50%;
		}
		.hamburger-content-inner{
			padding: 30px 0 60px;
		}
		// Transition Sub-Menu
		.main-menu{
			margin: 0;
			& > li{
				position: relative;
				& > a{
					font-size: 28px;
					font-family: $heading-font-family;
					position: relative;
					padding: 30px 30px;
					transition: font-weight ease 0.25s;
					@media only screen and (max-width: #{$header-size-breakpoint}){
						padding: 30px 7.5vw;
					}
					&::before{
						content: "";
						width: calc(100% - 60px);
						height: 1px;
						position: absolute;
						bottom: 0;
						left: 50%;
						transform: translateX(-50%);
						background-color: rgba($color: $white, $alpha: 0.5);
						transition: opacity ease 0.25s;
						@media only screen and (max-width: #{$header-size-breakpoint}){
							width: calc(100% - 15vw);
						}
					}
					&:hover,
					&:active
					&:focus{
						text-decoration: none;
					}
				}
				a{
					color: $white;
					display: block;
				}
				& > .avia_mega_div,
				& > .sub-menu{
					@media only screen and (max-width: #{$burger-breakpoint}){
						max-height: 0;
						margin: 0;
						opacity: 0;
						pointer-events: none;
						overflow: hidden;
						position: relative;
						padding-bottom: 0;
						transition: max-height cubic-bezier(0.4, 0, 0.2, 1) 0.3s, height cubic-bezier(0.4, 0, 0.2, 1) 0.3s, overflow ease 0.25s, opacity ease 0.25s, padding ease 0.25s;
					}
					li{
						a{
							padding: 10px 30px;
							position: relative;
							@media only screen and (max-width: #{$header-size-breakpoint}){
								padding: 10px 7.5vw;
							}
							&::before{
								content: '\e807';
								font-family: 'rxpharmacy-icons';
								position: absolute;
								top: 50%;
								right: 30px;
								transform: translateY(-50%);
								color: $green;
								@media only screen and (max-width: #{$header-size-breakpoint}){
									right: 7.5vw;
								}
							}
						}
					}
					&::before{
						content: "";
						width: calc(100% - 60px);
						height: 1px;
						position: absolute;
						bottom: 0;
						left: 50%;
						transform: translateX(-50%);
						background-color: rgba($color: $white, $alpha: 0.5);
						opacity: 0;
						transition: opacity ease 0.25s;
						@media only screen and (max-width: #{$header-size-breakpoint}){
							width: calc(100% - 15vw);
						}
					}
				}
				&.sub-menu-is-active{
					& > a{
						font-weight: $bold;
						&::before{
							opacity: 0;
						}
					}
					& > .avia_mega_div,
					& > .sub-menu{
						padding-bottom: 30px;
						@media only screen and (max-width: #{$burger-breakpoint}){
							max-height: 500px;
							pointer-events: initial;
							overflow: visible;
							opacity: 1;
							transition-duration: 0.4s;
						}
						&::before{
							opacity: 1;
						}
					}
					.sub-menu-indicator{
						&:before{
							transform: rotate(180deg);
						}
					}
				}
				.sub-menu-indicator{
					width: 40px;
					height: 40px;
					position: absolute;
					right: 30px;
					top: 25px;
					display: flex;
					align-items: center;
					justify-content: center;
					cursor: pointer;
					@media only screen and (max-width: #{$header-size-breakpoint}){
						right: 7.5vw;
					}
					&:before{
						content: "\e800";
						font-family: "fa-fontello";
						font-size: 30px;
						color: $white;
						position: absolute;
						transition: transform ease 0.25s;
					}
				}
			}
		}
		.ep-social-bookmarks-wrapper{
			margin-top: 40px;
			.ep-social-bookmarks{
				flex-direction: row;
				li{
					&:not(:last-child){
						margin-right: 45px!important;
					}
					a{
						color: rgba($color: $white, $alpha: 0.32);
						transition: opacity ease 0.25s;
						&::before{
							font-size: 37px;
						}
						&:hover{
							opacity: 0.5;
						}
					}
				}
			}
		}
	}

	.header{
		position: fixed;
		background: transparent;
		z-index: 12;
		width: 100%;
		transition: all ease 0.2s;
        .announcement-banner{
			height: $announcement-banner;
			background-color: $pink5;
			display: none;
			justify-content: center;
			align-items: center;
			position: relative;
			.announcement-banner-inner{
				max-width: 1600px;
				width: 100%;
				margin: 0 auto;
				padding: 0 50px;
				position: relative;
				text-align: center;
				color: $white;
				font-weight: 600;
                font-size: 20px;
                line-height: 1.5em;
                text-transform: uppercase;
                font-family: $heading-font-family;
                letter-spacing: calc( ( 20px / 1000 ) * 150 );
                display: flex;
                justify-content: center;
                align-items: center;
				.banner-button{
					color: inherit;
					transition: opacity ease 0.25s;
					position: relative;
                    text-decoration: underline;
                    margin-left: 7px;
					&:hover{
						opacity: 0.7;
					}
				}
                i.icon-temp::before {
                    content: "\E82B";
                    font-family: 'rxpharmacy-icons';
                    margin-right: 19.5px;
                    font-size: 33px;
                }
				.close-bar{
					position: absolute;
					top: 50%;
					right: 0;
					transform: translateY(-50%);
					background-color: transparent;
					border: 0;
					color: $white;
					font-weight: $bold;
					font-size: 20px;
					cursor: pointer;
                    display: none;
				}
			}
		}
		&::before{
			content: '';
			height: 100%;
			width: 100%;
			position: absolute;
			left: 0;
			top: 0;
			background-color: $lightblue3;
			opacity: 0;
			visibility: hidden;
			transition: inherit;
			box-shadow: 0 0 5px rgba($color: $black, $alpha: 0.1);
			z-index: inherit;
		}
		.header-inner{
			display: flex;
			align-items: center;
			justify-content: space-between;
			height: $header-size-mobile;
			transition: all ease 0.2s;
			z-index: 12;
			position: relative;
			padding: 20px 7.5vw;
			max-width: 1310px;
			margin: 0 auto;
			@media only screen and (min-width: #{$header-size-breakpoint+1}){
				height: $header-size;
			}
			@media only screen and (min-width: #{$header-size-breakpoint+1}){
				padding: 20px 50px;
			}
			.header-logo{
				position: relative;
				.logo{
					display: block;
					max-width: 200px;
					transition: all ease 0.25s;
					@media only screen and (max-width: 767px){
						max-width: 150px;
					}
					img{
						display: block;
					}
				}
			}
			.header-menu{
				@media only screen and (max-width: #{$burger-breakpoint}){
					display: none;
				}
				.main-menu{
					display: flex;
					flex-direction: row;
					align-items: center;
					justify-content: space-between;
					border-radius: 40px;
					box-shadow: -5px -5px 15px rgba($color: $white, $alpha: 1);
					margin: 0;
					padding: 10px 10px;
					min-width: 120px;
					position: relative;
					&::before{
						content: "";
						width: 100%;
						height: 100%;
						position: absolute;
						top: 0;
						right: 0;
						border-radius: 40px;
						background: linear-gradient(142deg, #e2f1fa 0%, #d2e5f1 100%);
						box-shadow: inset 0 3px 6px rgba($color: $white, $alpha: 0.18), 5px 5px 15px rgba($color: $black, $alpha: 0.24) ;
					}
					@media only screen and (min-width: 768px){
						padding: 15px 40px;
						min-width: 246px;
					}
					& > li{
						position: relative;
						&:not(:last-child){
							margin-right: 47px;
						}
						& > a{
							font-size: 15px;
							font-family: $body-font-family;
							font-weight: $bold;
							color: $blue2;
							transition: color ease 0.25s;
							&:hover{
								text-decoration: none;
								color: $blue;
							}
							@media only screen and (min-width: 768px){
								font-size: 18px;
							}
						}
						&.menu-item-has-children{
							.sub-menu{
								position: absolute;
								top: calc( 50% + calc( #{$header-size} / 2 ) );
								right: 0;
								transform: translateY(-10px);
								margin: 0;
								opacity: 0;
								pointer-events: none;
								background-color: $white;
								transition-property: opacity, transform;
								transition-timing-function: ease-in-out;
								transition-duration: 0.25s;
								z-index: 1;
								width: max-content;
								min-width: 259px;
								border-radius: 20px;
								box-shadow: 0px 15px 60px #cbdbe9;
								padding: 30px 0 20px;
								&::before{
									content: "";
									width: 112px;
									height: 10px;
									border-radius: 5px;
									background-color: $pink3;
									position: absolute;
									top: -4px;
									right: 29px;
									transform: translateY(-50px);
									opacity: 0;
									transition: transform ease 0.25s, opacity ease 0.25s;
								}
								& > li{
									& > a{
										color: $blue3;
										font-size: 18px;
										display: block;
										padding: 10px 31px;
									}
								}

							}
							&.sub-menu-is-active{
								.sub-menu{
									opacity: 1;
									pointer-events: initial;
									transform: translateY(-40px);
									&::before{
										transform: translateY(0);
										opacity: 1;
									}
								}
							}
						}
					}
				}
			}
			.hamburger-toggle{
				width: 20px;
				height: 16px;
				padding: 0;
				position: relative;
				background: transparent;
				border: none;
				outline: none;
				border-radius: 0;
				cursor: pointer;
				@media only screen and (min-width: #{$burger-breakpoint+1}){
					display: none;
				}
				.burger-box{
					width: 100%;
					height: 2px;
					background: $blue2;
					transition: all ease 0.2s;
					&::before,
					&::after{
						content: '';
						height: 2px;
						width: 100%;
						background: $blue2;
						position: absolute;
						transition: all ease 0.2s;
					}
					&::before{
						top: 0;
						left: 0;
					}
					&::after{
						bottom: 0;
						left: 0;
					}
				}
			}
		}
        &.has-bar{
			transition: ease 0.25s;
			.announcement-banner{
				@media only screen and (min-width: 768px) and (min-width: #{$header-size-breakpoint+1}){
					display: flex;
				}
			}
		}
	}

	&.header-is-scrolled{
		.header{
			&::before{
				opacity: 1;
				visibility: visible;
			}
			.header-inner{
				height: $header-size-mobile;
				.header-logo{
					.logo{
						@media only screen and (min-width: 768px){
							max-width: 156px;
						}
					}
				}
			}
            &.has-bar{
                @media only screen and (min-width: #{$burger-breakpoint+1}){
                    transform: translateY(calc( #{$announcement-banner} * -1));
                }
            }
		}
		.hamburger-content{
			top: $header-size-mobile;
		}
        
	}

	&.html_av_admin_bar_active{
		.hamburger-content{
			top: calc( #{$header-size-mobile} + 46px );
			@media only screen and (min-width: #{$header-size-breakpoint+1}){
				top: calc( #{$header-size} + 32px );
			}
		}
		&.header-is-scrolled{
			.hamburger-content{
				top: calc( #{$header-size-mobile} + 46px );
				@media only screen and (min-width: #{$header-size-breakpoint+1}){
					top: calc( #{$header-size-mobile} + 32px );
				}
			}
		}
	}

	&.burger-is-active{
		overflow: hidden;
		.hamburger-overlay{
			@media only screen and (min-width: #{$header-size-breakpoint+1}){
				opacity: 1;
				pointer-events: initial;
			}
        }
		.header{
			&::before{
				opacity: 1;
				visibility: visible;
			}
			.header-inner{
				.hamburger-toggle{
					.burger-box{
						background: transparent;
						&::before,
						&::after{
							top: calc( 50% - 2px );
						}
						&::before{
							transform: rotate(45deg);
						}
						&::after{
							bottom: auto;
							transform: rotate(-45deg);
						}
					}
				}
			}
			.hamburger-content{
				pointer-events: auto;
				-webkit-overflow-scrolling: touch;
				opacity: 1;
				visibility: visible;
				overflow-y: auto;
				right: 0;
			}
		}
	}

}

/* Space Top */
.html_header_top.html_header_sticky #main{
	padding-top: 0 !important;
}

#top:not(.page-template-template-blank) #main > .container_wrap_first:first-child .container, 
#top:not(.page-template-template-blank) .avia-builder-el-0 > .container,
#top:not(.page-template-template-blank) .avia-builder-el-0 > .av-section-color-overlay-wrap > .container,
#top:not(.page-template-template-blank) .avia-builder-el-0 .slideshow_caption{
	padding-top: $header-size-mobile;
	@media only screen and (min-width: #{$header-size-breakpoint+1}){
		padding-top: $header-size;
	}
}

@media only screen and (max-width: 1120px){
	#header{
		.units{
			width: 100% !important; /* custom header fix due to #header */
		}
	}
}

[id]:target,
[name]:target{
	&:before{
		content: '';
		display: block;
		height: calc( #{$header-size-mobile} + 80px );
		margin-top: calc( ( #{$header-size-mobile} + 80px ) * -1 );
		visibility: hidden;
	}
}

#top:not(.page-template-template-blank) .header.has-bar + #main > .avia-builder-el-0{
	@media only screen and (min-width: #{$burger-breakpoint}){
		padding-top: #{$announcement-banner};
	}
}