/**
* These are helper CSS classes for misc use, almost all properties carry the !important rule here
* Keep it dead simple
*/

.bring-above {
	position: relative !important;
	z-index: 4 !important;
}

.narrow > .container,
.avia_textblock.narrow,
.flex_column_table.narrow {
	max-width: 1000px !important;
	margin-left: auto !important;
	margin-right: auto !important;
	float: none;
}

.av_inherit_color * {
	color: inherit !important;
}

.nolink {
	cursor: pointer !important;
	pointer-events: none !important;
}

.has-box-shadow{
	box-shadow: $box-shadow;
}

.two-col-ul {
	@media only screen and (min-width: 480px) {
		column-count: 2 !important;
	}
}

.no-bg{
	background-image: none !important;
}

.no-bg-mobile{
	@media only screen and (max-width: 767px) {
		background-image: none !important;
	}
}

.no-bg-tablet{
	@media only screen and (max-width: 989px) {
		background-image: none !important;
	}
}

.no-br-mobile br{
	@media only screen and (max-width: 767px){
		display: none !important;
	}
}


.fwd-phone{
	@media only screen and (max-width: 480px){
		margin-left: -7.5vw !important;
		margin-right: -7.5vw !important;
		width: auto !important;
	}
}

.fwd-mobile{
	@media only screen and (max-width: 767px){
		margin-left: -7.5vw !important;
		margin-right: -7.5vw !important;
		width: auto !important;
	}
}

.fwd-tablet{
	@media only screen and (max-width: 989px){
		margin-left: -7.5vw !important;
		margin-right: -7.5vw !important;
		width: auto !important;
	}
}

	
.img-has-shadow{
	img{
		box-shadow: -5px 10px 25px rgba($color: $black, $alpha: 0.16);
	}
}

.img-fwd-phone{
	.avia-image-container-inner{
		&, img{
			@media only screen and (max-width: 480px){
				width: 100% !important;
			}
		}
	}
}

.img-fwd-mobile{
	.avia-image-container-inner{
		&, img{
			@media only screen and (max-width: 767px){
				width: 100% !important;
			}
		}
	}
}

.img-fwd-tablet{
	.avia-image-container-inner{
		&, img{
			@media only screen and (max-width: 989px){
				width: 100% !important;
			}
		}
	}
}

.have-boxed-separators{
	.flex_column{
		.flex-column-inner{
			position: relative;
			.avia_textblock{
				p{
					font-size: 16px;
					line-height: 1.75;
				}
			}
			&::before{
				content: '\f1d8';
				font-family: 'fa-fontello';
				position: absolute;
				top: 0;
				width: 70px;
				height: 70px;
				border-radius: 16px;
				background: linear-gradient(#69b3f5 0%, #5ed1d8 100%);
				box-shadow: 0px 15px 30px rgba(56, 100, 125, 0.27);
				display: flex;
				justify-content: center;
				align-items: center;
				color: $white;
				font-size: 35px;
			}
		}
		&:first-child{
			@media only screen and (max-width: 767px){
				margin-bottom: 50px!important;
			}
			.flex-column-inner{
				@media only screen and (max-width: 767px){
					padding-left: 110px;
				}
				@media only screen and (min-width: 768px){
					padding-right: 110px;
				}
				.avia_textblock{
					p{
						margin: 0 auto 0 0!important;
						@media only screen and (max-width: 767px){
							text-align: left!important;
						}
						@media only screen and (min-width: 768px){
							max-width: 343px;
							margin: 0 0 0 auto!important;
						}
					}
				}
				&::before{
					left: 0;
					right: auto;
					@media only screen and (min-width: 768px){
						left: auto;
						right: 0;
					}
				}
			}
		}
		&:last-child{
			.flex-column-inner{
				padding-left: 110px;
				.avia_textblock{
					p{
						margin: 0 auto 0 0!important;
						@media only screen and (min-width: 768px){
							max-width: 388px;
						}
					}
				}
				&::before{
					left: 0;
				}
			}
		}
	}
}

.boxed-with-separator{
	.flex_column{
		.flex-column-inner{
			background-color: $white;
			min-height: 324px;
			border-radius: 20px;
			max-width: 270px;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			padding: 20px;
			position: relative;
			height: 100%;
		}
		
	}
}

.is-light-weight{
	p, .av-special-heading-tag{
		font-weight: $light;
	}
}

.separator--white{
	position: relative;
	&::before{
		content: "";
		width: 100%;
		height: 6px;
		background-color: rgba($color: $white, $alpha: 0.07);
		filter: drop-shadow(0px 10px 30px #000);
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 0;
		border-radius: 50px;
	}
}

.separator--blue{
	position: relative;
	&::before{
		content: "";
		width: 100%;
		height: 6px;
		background-color: rgba($color: $blue2, $alpha: 0.2);
		filter: drop-shadow(0px 10px 30px #000);
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 0;
		border-radius: 50px;
	}
}

#top .img--border-radious{
	img{
		border-radius: 20px;
	}
} 

#top .img--has-shadow{
	img{
		box-shadow: 0px 15px 60px #cbdbe9;
	}
}

#top .dark-social-icons{
	.ep-social-bookmarks{
		li{
			a{
				color: $darkblue;
			}
		}
	}
}

