html{
	--scrollBarWidth: 0px;
	--containerWidth: #{$container};
	--containerSingleWidth: #{$container-single};
}

.responsive .container{
    max-width: $container;
}

#top .avia-section.avia-shadow{
	box-shadow: none;
}

.avia_textblock{
	p{
		& > a{
			text-decoration: underline;
		}
	}
}  
strong{
	font-weight: $bold;
}

blockquote{
	font-size: 22px;
}

#top .av-share-box{
	margin: 0;
	float: none;
	.av-share-link-description{
		display: none;
	}
	.av-share-box-list{
		border: none;
		display: flex;
		flex-direction: row;
		li{
			border: none;
			&:not(:last-child){
				margin-right: 15px;
			}
			a{
				padding: 0;
				width: 40px;
				height: 40px;
				line-height: 40px;
				text-align: center;
				font-size: 18px;
			}
			.avia_hidden_link_text,
			.avia-related-tooltip{
				display: none !important;
			}
		}
	}
}

.ep-social-bookmarks-wrapper{
	ul{
		display: flex;
		flex-direction: row;
		li{
			&:not(:last-child){
				margin-right: 15px!important;
				@media only screen and (min-width: 768px){
					margin-right: 34px!important;
				}
			}
			a{
				color: $blue2;
				transition: color ease 0.25s;
				&:hover{
					color: $blue;
				}
			}
			&.av-social-link-facebook{
				a{
					&::before{
						content: '\f09a'!important;
					}
				}
			}
			&.av-social-link-linkedin{
				a{
					&::before{
						content: '\f0e1'!important;
					}
				}
			}
		}
	}
}

.ep-social-bookmarks-wrapper,
.av-share-box{
	ul{
		li{
			&.av-social-link-facebook{
				a{
					&::before{
						content: '\f09a'!important;
					}
				}
			}
			&.av-social-link-linkedin{
				a{
					&::before{
						content: '\f0e1'!important;
					}
				}
			}
		}
	}
}

.ep-flickity-slider{
	@media only screen and (max-width: 767px){
		padding-bottom: 47px;
	}
	.flickity-page-dots{
		bottom: 0;
		left: var(--columnGap);
		justify-content: space-between;
		display: flex;
		
		&::before{
			content: "";
			width: 100%;
			height: 3px;
			position: absolute;
			bottom: 0px;
			left: 0;
			background-color: $lightblue;
			z-index: -1;
		}
		.dot{
			width: 103px;
			height: 3px;
			border-radius: 5px;
			bottom: 0;
			z-index: 2;
			background-color: $pink3;
			opacity: 0;
			margin: 0;
			&.is-selected{
				opacity: 1;
			}
		}
	}
}