#top{
	font-family: $body-font-family;
	font-weight: $regular;  
	font-size: 18px;
	
	h1, h2, h3, h4, h5, h6,
	.h1, .h2, .h3, .h4, .h5, .h6{
		font-family: $heading-font-family;
		text-transform: none;
		font-weight: $bold;
	}

	h1, .h1 {
		font-size: $h1-tablet;
		line-height: 65px / $h1-tablet;
		font-weight: $light;
		@media only screen and (min-width: 1100px){
			font-size: $h1;
			line-height: 65px / $h1;
		}
	}
	
	h2, .h2 {
		font-size: $h2-tablet;
		line-height: 46px / $h2-tablet;
		font-weight: $light;
		@media only screen and (min-width: 1100px){
			font-size: $h2;
			line-height: 1.27;
		}
	}
	
	h3, .h3 {
		font-size: $h3-tablet;
		line-height: 34px / $h3-tablet;
		@media only screen and (min-width: 1100px){
			font-size: $h3;
			line-height: 34px / $h3;
		}
	}
	
	h4, .h4 {
		font-size: $h4;
		line-height: 30px / $h4;
		letter-spacing: calc((#{$h4} / 1000 ) * 150);
	}
	
	h5, .h5 {
		font-size: $h5;
		line-height: 27px / $h5;
	}
	
	h6, .h6 {
		font-size: $h6;
		line-height: 20px / $h6;
		letter-spacing: calc((#{$h6} / 1000 ) * 150);
		text-transform: uppercase;
	}
	
	.av-subheading{
		letter-spacing: 3px;
		text-transform: uppercase;
		font-family: $heading-font-family;
		font-size: 16px;
	}

	.ep-custom-menu-element,
	.av-share-box,
	.ep-social-bookmarks-wrapper,
	.ep-icon-list-container{
		ul{
			margin: 0;
			li{
				list-style: none;
				padding: 0;
				margin: 0;
			}
		}
	}

	p, .entry-content-wrapper li{
		strong{
			font-weight: $bold;
		}
		a:not(.avia-button){
			transition: color ease 0.2s;
			&:hover{
				
			}
		}
	}

	em{
		font-style: italic;
	}
	
	p{
		margin: 0 0 0.85em 0;
	}

	.entry-content-wrapper{
		ul:not(.blocks-gallery-grid){
            margin: 0;
            padding: 0;
            li:not(.ep-iconlist-item):not(.ep-social-bookmarks):not(.av-share-link):not(.menu-item){
                padding: 0 0 0 20px;
                list-style: none;
                position: relative;
                margin: 1em 0;
                &::before{
                    content: '';
					font-family: '';
					position: absolute;
					left: 0;
					top: 11px;
					font-size: 0.8em;
					width: 9px;
					height: 9px;
					border-radius: 100%;
                }
            }
        }
        ol:not(.flickity-page-dots){
            margin: 0 0 0 1.2em;
            padding: 0;
            li{
                list-style: decimal;
                padding-left: 0.5em;
                &::before{
                    display: none;
                }
            }
        }
	}


	.main_color{
		background-color: $white;
		color: $darkblue2;

		a:not([class*='button']){
			color: $blue2;
			&:hover{
				color: $pink4;
			}
		}

		strong{
			color: $darkblue2;
		}

		.entry-content-wrapper{
			ul:not(.blocks-gallery-grid){
				li:not(.ep-iconlist-item):not(.ep-social-bookmarks):not(.av-share-link):not(.menu-item){
					&::before{
						background-color: $pink3;
					}
					ul{
						li{
							&::before{
								background-color: $blue3;
							}
						}
					}
				}
			}
		}

		h1, h2, h3, h4, h5, h6,
		.h1, .h2, .h3, .h4, .h5, .h6{

		}

		h1, .h1{
			color: $pink4;
		}
		h2, .h2{
			color: $darkblue2;
		}
		h3, .h3{

		}
		h4, .h4{
			color: $darkblue;
		}
		h5, .h5{
			color: $darkblue;
		}
		h6, .h6{
			color: $pink4;
		}
	}

	.alternate_color{
		background-color: $darkblue2;
		color: $white;
		
		a:not([class*='button']){
			color: $white;
		}

		.entry-content-wrapper{
			ul:not(.blocks-gallery-grid){
				li:not(.ep-iconlist-item):not(.ep-social-bookmarks):not(.av-share-link):not(.menu-item){
					&::before{
						background-color: $white;
					}
					ul{
						li{
							&::before{
								background-color: rgba($color: $white, $alpha: 0.37);
							}
						}
					}
				}
			}
		}

		h1, h2, h3, h4, h5, h6,
		.h1, .h2, .h3, .h4, .h5, .h6{
			
		}

		h1, .h1{

		}
		h2, .h2{

		}
		h3, .h3{

		}
		h4, .h4{

		}
		h5, .h5{

		}
		h6, .h6{
			
		}
	}

}
